<template>
  <section class="filters">
    <div class="wrapper-filters" :class="{'remove-guide': loginIn}">
      <v-expansion-panels flat multiple variant="accordion">
        <DescriptionBlock @click.prevent="setNext"></DescriptionBlock>
        <UploadImages></UploadImages>
        <ResolutionBlock></ResolutionBlock>
        <GenerationParameters></GenerationParameters>
        <Settings></Settings>
      </v-expansion-panels>
    </div>
    <VTour :steps="tourSteps" autoStart highlight ref="tour" v-if="!person.id">
      <template #actions="{}">
        <div class="vjt-actions">
        </div>
      </template>
    </VTour>
    <section class="main-buttons" id="v-step-1" @click.prevent="setNext">
      <button v-if="!loadingMore" class="create-project" @click="generate">
        <p class="coast-generation">Стоимость генерации {{ coastGeneration }} </p>
        {{ textMainButton }} {{ counterImage }}
        <v-icon end icon="mdi-arrow-right"></v-icon>
      </button>
      <button v-else class="create-project no-hover">
        <div class="wrapper-loader">
          <span>Генерация...</span>
          <svg viewBox="25 25 50 50">
            <circle r="20" cy="50" cx="50"></circle>
          </svg>
        </div>
      </button>
    </section>
    <DoneSnackBar
        :openSnackBar="openSnackBarDone"
        :text-snack-bar="textSnackBarForGeneration"
        @close="closeSnackBars">
    </DoneSnackBar>
  </section>

  <RegistrationDialog v-if="isOpenRegistrationDialog"
                      @closeRegistrationBlock="isOpenRegistrationDialog = false"></RegistrationDialog>
</template>

<script setup>
import ResolutionBlock from "@/components/text-to-image/parameters/ResolutionBlock";
import GenerationParameters from "@/components/text-to-image/parameters/GenerationParameters";
import Settings from "@/components/text-to-image/parameters/SettingsBlock"
import DescriptionBlock from "@/components/text-to-image/parameters/DescriptionBlock";
import UploadImages from "@/components/text-to-image/parameters/UploadImages";
import RegistrationDialog from "@/components/dialogs/RegistrationDialog";
import DoneSnackBar from "@/components/sneckbars/DoneSnackBar";
import {personStore} from "@/store/personStore";
import {ref, watch} from "vue";
import {storeToRefs} from "pinia/dist/pinia";
import requests from "@/mixins/requests";

const {generateImage} = requests();
const store = personStore();
const {person, filters} = storeToRefs(store);

let openSnackBarDone = ref(false);
let counterImage = ref('');
let textSnackBarForGeneration = ref('');
let isOpenRegistrationDialog = ref(false);
let textMainButton = ref('Сгенерировать');
let loadingMore = ref(false);
let coastGeneration = ref(5);
let loginIn = ref(false);
const tour = ref(null);

let tourSteps = ref();

if (window.innerWidth <= 650) {
  tourSteps.value = [
    {
      target: '#v-step-0',
      content: 'Начните с составления описания для генерации необходимых изображений',
      placement: 'top'
    },
    {
      target: '#v-step-1',
      content: 'Далее для генерации изображения нажмите на кнопку',
      placement: 'top',
    }
  ]
} else {
  tourSteps.value = [
    {
      target: '#v-step-0',
      content: 'Начните с составления описания для генерации необходимых изображений',
      placement: 'right'
    },
    {
      target: '#v-step-1',
      content: 'Далее для генерации изображения нажмите на кнопку',
      placement: 'right',
    }
  ]
}

function setNext() {
  if (!person._value.id) {
    tour.value.nextStep();
  }
}

function closeSnackBars() {
  openSnackBarDone.value = false;
}

watch(filters, (newData) => {
  if (newData.parameters.countImages === 1) {
    counterImage.value = `${newData.parameters.countImages} изображение`;
  } else if (newData.parameters.countImages > 1 && newData.parameters.countImages < 5) {
    counterImage.value = `${newData.parameters.countImages} изображения`;
  } else {
    counterImage.value = `${filters._value.parameters.countImages} изображений`;
  }
})

watch(person, (newDataPerson) => {
  if (newDataPerson.id) {
    loginIn.value = true;
  } else {
    loginIn.value = false;
    if (window.innerWidth <= 650) {

      tourSteps.value = [
        {
          target: '#v-step-0',
          content: 'Начните с составления описания для генерации необходимых изображений',
          placement: 'top'
        },
        {
          target: '#v-step-1',
          content: 'Далее для генерации изображения нажмите на кнопку',
          placement: 'top',
        }
      ]
    } else {
      tourSteps.value = [
        {
          target: '#v-step-0',
          content: 'Начните с составления описания для генерации необходимых изображений',
          placement: 'right'
        },
        {
          target: '#v-step-1',
          content: 'Далее для генерации изображения нажмите на кнопку',
          placement: 'right',
        }
      ]
    }
  }
})

function generate() {
  if (person._value.id) {
    generateImage(filters._value);
    const blockImages = document.getElementById('images')?.offsetTop;
    window.scrollTo({
      top: blockImages,
      behavior: "smooth",
    })
    loadingMore.value = true;
    setTimeout(() => {
      loadingMore.value = false;
      textSnackBarForGeneration.value = "Изображение сгенерировано";
      openSnackBarDone.value = true;
    }, 2000)
  } else {
    isOpenRegistrationDialog.value = true;
  }
}

</script>

<style lang="scss">
#vjt-tooltip {
  background-color: #37a3b5;
  border-radius: 10px;
  margin-left: 20px !important;
  padding: 15px;
  font-weight: 700;
  color: var(--main-light-color);
  z-index: 1;
}

#vjt-arrow::before {
  content: '';
  width: 15px;
  height: 15px;
  top: -3px;
  background-color: #37a3b5;
  transform: rotate(45deg);
}

#vjt-arrow::after {
  display: none;
}

.vjt-highlight {
  outline-offset: 5px;
  outline: var(--light-blue) dashed 2px;
}

.remove-guide {
  .vjt-highlight {
    outline: none;
  }
}

.filters {
  width: 350px;
  height: calc(100vh - 70px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #2b2b2be5;
  border-radius: 0 30px 30px 0;
  padding: 30px 0 0 0;
  margin-top: 70px;

  .wrapper-filters {
    overflow-x: hidden;
    overflow-y: auto;
    scrollbar-gutter: stable;

    &::-webkit-scrollbar {
      width: 2px;
      height: 1000px;
    }

    &::-webkit-scrollbar-track {
      background: #404040E5;
    }
  }

  .main-buttons {
    min-height: 70px;
    position: relative;

    .create-project {
      border-radius: 0 0 30px 0;

      &:hover {
        box-shadow: 0 0 15px rgba(54, 226, 255, 0.7);
      }

      .coast-generation {
        display: flex;
        align-items: center;
        height: 100%;
        border-right: 1px solid;
        font-size: 10px;
      }
    }

    .no-hover {
      cursor: wait;

      &:hover {
        box-shadow: unset;
      }
    }
  }
}

.wrapper-loader {
  display: flex;
  align-items: center;
  gap: 30px;

  svg {
    width: 40px;
    transform-origin: center;
    animation: rotate4 2s linear infinite;
  }

  circle {
    fill: none;
    stroke: var(--main-light-color);
    stroke-width: 2;
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
    stroke-linecap: round;
    animation: dash4 1.5s ease-in-out infinite;
  }

  @keyframes rotate4 {
    100% {
      transform: rotate(360deg);
    }
  }

  @keyframes dash4 {
    0% {
      stroke-dasharray: 1, 200;
      stroke-dashoffset: 0;
    }

    50% {
      stroke-dasharray: 90, 200;
      stroke-dashoffset: -35px;
    }

    100% {
      stroke-dashoffset: -125px;
    }
  }
}

@media screen and(max-width: 700px) {
  #vjt-tooltip {
    margin-left: 5px !important;
    padding: 12px;
  }
}

@media screen and(max-width: 650px) {

  #vjt-tooltip {
    margin-left: 0 !important;
    margin: 10px 0 !important;
    padding: 12px;
  }

  .filters {
    width: 100%;
    border-radius: 0;
    height: auto;

    .wrapper-filters {

    }

    .main-buttons {
      border-radius: 0;
      align-items: center;

      .create-project {
        border-radius: 0;
        gap: 20px;

        .coast-generation {
          padding: 0 10px;
        }
      }
    }
  }
}
</style>
